let image = 'https://www.sweetqianba.com/group1/M00/00/00/rBsAEV0ATOeAXBxrAAAGhv7fj6Y908.png';
import {
	user
} from '../../setting/user.js'
export default {
	data() {
		return {
			image: image,
			loginStatus: false,
			icons: user.icons,
			userInfo: {},
			iconList: user.iconList,
		}
	},
	onShow() {
		let token = uni.getStorageSync('token');
		let userInfo = uni.getStorageSync('userInfo');
		if (!token) {
			this.loginStatus = false;
		} else {
			this.loginStatus = true;
			this.userInfo = userInfo;
		}
	},
	methods: {
		goSetting() {
			if (this.loginStatus) {
				uni.navigateTo({
					url: 'setting/setting'
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '请先登录'
				})
			}
		},
		goUserInfo() {
			if (this.loginStatus) {
				uni.navigateTo({
					url: 'userInfo/userInfo'
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '请先登录'
				})
			}
		},
		goAvatar() {
			if (this.loginStatus) {
				// 显示头像的模态框，即预览头像
			} else {
				this.bindLogin();
			}
		},
		bindLogin() {
			uni.navigateTo({
				url: 'login/login',
			});
		},
		bindLogout() {
			if (this.forcedLogin) {
				uni.reLaunch({
					url: 'login/login',
				});
			}
		},
		goWallet() {
			uni.navigateTo({
				url: 'wallet/wallet'
			})
		},
		goCollection() {
			uni.showToast({
				icon: 'none',
				title: '暂未开放'
			})
		},
		goEdit() {
			uni.navigateTo({
				url: '../evaluateModel/evaluate'
			})
		},
		goCoupon() {
			uni.showToast({
				icon: 'none',
				title: '暂未开放'
			})
		},
		goMessage() {
			uni.showToast({
				icon: 'none',
				title: '暂未开放'
			})
		},
		method(data) {
			this[data]()
		},
	}
}
